/*eslint-disable*/
import React from "react";

// reactstrap components
import { Container } from "reactstrap";
// core components

function IndexHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth > 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  return (
    <>
      <div className="page-header clear-filter" filter-color="blue">
        <div
          className="page-header-image"
          style={{
            backgroundImage: "url(" + require("assets/img/blue2.png") + ")"
          }}
          ref={pageHeader}
        ></div>
        <Container>
          <div className="content-space-around brand">
          <h1><br/></h1>
          <h1 class="h1-seo">RADIOÏDE</h1>
          <h6 class="h6-seo">Comprendre, Débattre, Imaginer</h6>
          <h1><br/></h1>
          <h3 class="h3-seo">POUR UNE NOUVELLE RADIO DANS TOUTE LA FRANCE</h3>
          <p>Pour la création d'un programme de radio associative et citoyenne sur tout le territoire français en réponse à l'appel du Conseil Supérieur de l'Audiovisuel. Le C.S.A. organise les ondes hertziennes - comme la FM, le <a href="https://www.dabplus.fr/">DAB+</a> - et lance un appel aux propositions.</p> 
          <h1><br/></h1>
          <h6 class="h6-seo"><big>Mobilisons nous pour que les ondes hertziennes ne soient pas entièrement attribuées aux radios commerciales et publiques.</big></h6>
          </div>
        </Container>
      </div>
    </>
  );
}

export default IndexHeader;
